import React, { Component } from 'react';
import greek_flag from './greek_flag.gif';
import './App.css';
import Keyboard from 'react-simple-keyboard';
import 'react-simple-keyboard/build/css/index.css';
import {Greek} from './greek'
import {greeklayout} from './layouts'

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      norwegian: '',
      greek: '',
      transcriber: new Greek(''),
      language: 'greek',
      layoutName: "default",
    }
  }

  changeGreek = (event) => {
    this.setState({greek: event.target.value})
    this.state.transcriber.setString(event.target.value)
  }

  KBonChange = (input) => {
    this.forceUpdate()
  }

  KBonKeyPress = (button) => {
    if (["{lock}", "{shift}"].includes(button)) {
      this.handleShiftButton();
      this.state.transcriber.setString(this.state.greek)
      this.forceUpdate()
      return;
    }
    if (button === "{space}")
      button = " "
    if (button === "{delete}") {
      this.setState({greek: ""})
      this.state.transcriber.setString("")
      this.forceUpdate()
      return
    }
    if (button === "{bksp}") {
      this.setState({greek: this.state.greek.slice(0, -1)})
      this.state.transcriber.setString(this.state.greek)
    } else {
      this.setState({greek: this.state.greek + button})
      this.state.transcriber.setString(this.state.greek)
    }
  }
  handleShiftButton = () => {
    const shiftToggle = this.state.layoutName === "default" ? "shift" : "default";
    this.setState({ layoutName: shiftToggle });
  };

  render() {
    return (
  <div className="App">
    <div className="App-header">
      <img src={greek_flag} className="App-logo" alt="logo" />
      <h2>Transliterasjon av nygresk!</h2>
      <h3>Μεταγραφής από το Ελληνικό στο Νορβηγός</h3>
    </div>

    <div className="Greek-form">
      <form>
        <input type="text"
        value={this.state.greek}
        onChange={this.changeGreek}
        placeholder="Dette er jo helt gresk!"/>
      </form>
      <p>&nbsp;{this.state.transcriber.norwegian()}</p>
    </div>
    <Keyboard
          onChange={this.KBonChange}
          onKeyPress={this.KBonKeyPress}
          layoutName={this.state.layoutName}
          buttonTheme={[]}
          physicalKeyboardHighlight
          layout={greeklayout}
        />
    <div className="footer"
    >
      <p>Ikke bruk denne for noe viktig! Jeg kan ikke garantere den er 100% korrekt</p>
    </div>
    </div>
    );
  }
}

export default App;



